import React from 'react'
import { Table, Button, Avatar, Popconfirm, Form, Modal, Input,Icon,Rate,Pagination,Badge } from 'antd'
import '../../style/table.less'
import OrderCart from './modal/order_modal'
import {connect} from 'react-redux'
import {orDetails} from '../../redux/order_redux'
import { shopCart } from '../../redux/shop_redux'

const {confirm} = Modal;
const statusMap = ['default','error','processing','success']
const status = ['','','','']
@connect(
  state=>state,
  {orDetails,shopCart}
)
class WaitReceiveTable extends React.Component {
  constructor (props){
    super(props)
    this.state = {
      loading: false,
      sum:0,
      allprice:0,
      modalType:'',
      visible:false,
      footerNull:undefined,
      data:[{
        ordernumber:'0000000000000001',
        status:2,
        key: '1',
        avatar:['https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png',
          'https://gw.alipayobjects.com/zos/rmsportal/mqaQswcyDLcXyDKnZfES.png'
        ],
        name: 'PPR',
        unit:10,
        number:1,
        price:10,
        Specifications: '',
        operation:['','','','']
      },
        {
          ordernumber:'0000000000000001',
          status:2,
          key: '2',
          avatar:['https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png',
            'https://gw.alipayobjects.com/zos/rmsportal/mqaQswcyDLcXyDKnZfES.png'
          ],
          name: 'PPR',
          unit:20,
          number:1,
          price:20,
          Specifications: '',
          operation:['','','','']

        }
      ]
    }
  }
  componentDidMount(){

  }

  //
  details = (record) => {
    if ( this.props.details)
      this.props.details(record)
  }
  //moadl
  handleok= (val,value) => {
  console.log(this.state.modalType)
    if(this.state.modalType === ''){
      let ordernumber = this.props.shop.order.details.ordernumber
      this.props.orDetails(value)
      this.setState({
        visible:val
      })
    }
    else {
      this.setState({
        visible:val
      })
    }
  }
  //
  operation = (v,record,index) => {
    let _this = this               //onOK this
    if(v===''){
      confirm({
        title: '?',
        okText: '',
        okType: '',
        cancelText: 'No',
        onOk(){
          const dataChange = _this.state.data
          dataChange.splice(index, 1);
          _this.setState({
            data:dataChange
          })
        }
      })
    }
    if(v===''){
      this.props.orDetails(record)
      this.setState({
        visible:true,
        modalType:v
      })
    }
    if(v===''){
      this.props.orDetails(record)
      this.setState({
        visible:true,
        modalType:v
      })
    }
    if(v===''){
      this.props.orDetails(record)
      this.setState({
        visible:true,
        modalType:v
      })
    }
    if(v===''){
      this.props.orDetails(record)
      this.setState({
        visible:true,
        modalType:v
      })
    }
    if(v===''){
      this.props.orDetails(record)
      this.props.shopCart(record)
      this.setState({
        visible:true,
        modalType:v,
        footerNull:null
      })
    }
  }
  //
  onCancel = () => {
    this.setState({
      visible:false,
      footerNull:null
    })
  }
  //
  onDelete = (record,index) => {
    console.log(record)
    const dataChange = this.state.data
    dataChange.splice(index, 1);
    this.setState({
      data:dataChange
    })
  }
  render() {
    const { visible ,modalType, footerNull} = this.state
    const columns = [
      {
        title: '',
        dataIndex: 'ordernumber',
      },
      {
        title: '',
        dataIndex: 'status',
        filters:[
          {
            text:status[0],
            value:0
          },
          {
            text:status[1],
            value:1
          },
          {
            text:status[2],
            value:2
          },
          {
            text:status[4],
            value:4
          }
        ],
        render:(val) => {
          return <Badge status={statusMap[val]} text={status[val]}/>
        }
      },
      {
        title: '',
        dataIndex: 'avatar',
        render:(value,record)=>{
          return(
            <div>
              <Avatar src={record.avatar[0]}  ></Avatar>
            </div>
          )
        }
      },
      {
        title: '',
        dataIndex: 'name',
      },
      {
        title: '',
        dataIndex: 'Specifications',
      },
      {
        title: '',
        dataIndex: 'unit',
        render:val=>`${val}`
      },
      {
        title: '',
        dataIndex: 'number',
        render:(value,record) => {
          return(
            <div >
             {value}
            </div>
          )
        }
      },
      {
        title: '',
        dataIndex: 'price',
        render:val=>`${val}`
      },
      {
        title: '',
        dataIndex: 'details',
        render:(value,record) => {
          return(
            <div>
              <Button onClick={()=>this.operation('',record)}></Button>
            </div>
          )
        }
      },
      {
        title: '',
        dataIndex:'operation',
        render:(value,record,index)=>{
          return(
            <div>
              {record.operation.map(v=>(
                <Button key={v} type={v===''?'danger':null} style={{marginRight:'6px'}} onClick={()=>this.operation(v,record,index)}>{v}</Button>
              ))}
            </div>
          )
        }
      },
    ];
    return (
      <div className=" container_manager_address container_table congtainer_order">
        <Table
          columns={columns}
          dataSource={this.state.data}
          onDelete={this.onDelete}
          pagination={false}
        />
        <Pagination
          current={1} total={1} pageSize={1}
        />
        <OrderCart
          visible={visible}
          title={modalType}
          onOk={this.handleok}
          onCancel={this.onCancel}
          footerNull={footerNull}
        />
      </div>
    );
  }
}

export  default Form.create()(WaitReceiveTable)