import  React from 'react';
import  axios from 'axios'
import {
  Tabs,
  Form,
  Row,
  Col,
  Icon,
  Input,
  Button,
  Checkbox,
  Cascader
} from 'antd';
import { withRouter } from "react-router-dom"
import LogReg from '../../component/background/log_reg';
import './login.less'

    const options = [{
      value: '',
      label: '',
      children: [{
        value: '',
        label: '',
        children: [{
          value: '',
          label: '',
        }],
      }],
    }, {
      value: '',
      label: '',
      children: [{
        value: '',
        label: '',
        children: [{
          value: '',
          label: '',
        }],
      }],
    }];
@withRouter
class LoginForm extends React.Component {

  state = {
      count:0,
      autoLogin:true,
  }
  //
  ForgotPwd = () => {
    this.props.history.push('/forgotpwd')
  }
  //
  register = () => {
    this.props.history.push('/register')
  }
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log(values, '====>>>>>')
        const list = localStorage.getItem('address_list')
        const listArray = (JSON.parse(list) || []).map((v) => {
          return {
            ...v,
            default: false,
          }
        })
        listArray.push({
          id: listArray.length,
          key: listArray.length,
          name: '',
          phone: '13456801341',
          address: '',
          addressSelect: values.addressSelect,
          specificAddress: '123',
          default: true,
        })
        if (values.userName === 'admin') {
          localStorage.setItem('authList', ['buyer', 'admin'])
        } else {
          localStorage.setItem('authList', ['buyer'])
        }
        localStorage.setItem('address_list', JSON.stringify(listArray))
        console.log('Received values of form: ', values);
        this.props.history.push('/home')
      }
    });
  }
  //
  changgeAutoLogin = e => {
    this.setState({
      autoLogin:e.target.checked
    })
    console.log(this.autoLogin)
  }
  onTabChange = type => {
    this.setState({type})
  }

  // componentDidMount(){
  //
  //   axios.get("/api/test/profile")
  //     .then(res=>{
  //       // if (res.status==200&&res.data.code===0) {
  //       // 	dispatch(authSuccess(res.data.data))
  //       // }else{
  //       // 	dispatch(errorMsg(res.data.msg))
  //       // }
  //       console.log(res);
  //
  //     })
  //
  //
  // }
  render() {
    const { getFieldDecorator } = this.props.form;
    const TabPane = Tabs.TabPane;
    const FormItem = Form.Item;
    return (
      <LogReg>
        <div className="container_login">
          <div className="content">
            <div className="logo-top">
              <img src={require('../../component/img/logo.jpg')} alt="" />
              <h1></h1>
            </div>
            <Tabs
              defaultActiveKey={'account'}
            >
              <TabPane tab="" key="account" >
                <Form onSubmit={this.handleSubmit} className="login-form">
                  <FormItem>
                    {getFieldDecorator('userName', {
                      rules: [{ required: true, message: '!' }],
                    })(
                      <Input prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="" />
                    )}
                  </FormItem>
                  <FormItem>
                    {getFieldDecorator('password', {
                      rules: [
                        { required: true, message: '!' },
                        {min:6, message:'6'}
                        ],
                    })(
                      <Input prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} type="password" placeholder="" />
                    )}
                  </FormItem>
                  
                  <FormItem >
                    {getFieldDecorator('addressSelect',{
                      rules: [{ required: true, message: '!' }],
                    })(
                      <Cascader options={options}  placeholder="" />

                    )}
                  </FormItem>
                  <FormItem>
                    {getFieldDecorator('remember', {
                      valuePropName: 'checked',
                      initialValue: true,
                    })(
                      <Checkbox ></Checkbox>
                    )}
                    <span className="login-form-forgot">
                    <a onClick={this.ForgotPwd}></a>
                  </span>
                  </FormItem>
                  <FormItem >
                    <Button type="primary" htmlType="submit" className="login-form-button btn_280"></Button>
                  </FormItem>
                </Form>
              </TabPane>
            </Tabs>
            {/* <a></a> */}
            <a onClick={this.register} className="register"></a>
          </div>
        </div>
      </LogReg>

    );
  }
}

const Login = Form.create()(LoginForm)
export  default  Login
