import React from  'react'
import { List, Avatar,Button,Modal} from 'antd';
import PictureBrowing from '../modal/picture_browsing_modal'
import {connect} from 'react-redux'

import '../../style/details.less'

@connect(
  state=>state,
)
class EvaluateDetails extends React.Component{
    constructor(props){
      super(props)
      this.state = {
        previewVisible:false,
        previewImage:'',
        contentEva:false,
        data:[
          {
            title: 'Ant Design Title 1',
            description:'2018-10-22 10:23:56',
            content:'' +
              '' +
              '' +
              '100' +
              '',
            img:['https://gw.alipayobjects.com/zos/rmsportal/mqaQswcyDLcXyDKnZfES.png',
              'https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png'
            ]
          },
          {
            title: '',
            description:'2018-10-22 10:23:56',
            content:'100' +
              '100' +
              '' +
              ',' +
              '',
             img:['https://gw.alipayobjects.com/zos/rmsportal/mqaQswcyDLcXyDKnZfES.png',
               'https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png'
             ]
          },
        ]
      }
    }
  //
  toSee = (v) => {
    this.setState({
      previewVisible:true,
      previewImage: v,
    })
    console.log(v,this.state.previewVisible)
  }
  //
  toggleCentent = (item) => {
    if (item){
      const {contentEva} = this.state
      this.setState({
        contentEva:!contentEva
      })
    }
  }
  previewonCancel = () => this.setState({ previewVisible: false })
  render(){
    const evaluates = this.props.shop.evaluates
    const {previewVisible,previewImage,contentEva} = this.state
    return(
      <div className="container_details container_evaluate">
        {/*//home*/}
        {/*<div className="back">*/}
          {/*<Button type="primary" onClick={()=>this.props.history.goBack()}></Button>*/}
        {/*</div>*/}
        <div className="evaluate_top">
          <h3>{evaluates.id}</h3>
        </div>
        <List
          itemLayout="horizontal"
          dataSource={this.state.data}
          renderItem={item => (
            <List.Item
            >
              <List.Item.Meta
                avatar={<Avatar src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png" />}
                title={item.title}
                description={item.description}
              />
              <br/>
              <div >
                {contentEva === false?
                  <div className="evaluate_content">{item.content}</div>:null
                }
                {contentEva === true?
                <div>{item.content}</div>:null
                }
                <div className="toggleCentent" onClick={()=>this.toggleCentent(item)}>{contentEva === false?'':''}</div>
                <div style={{marginTop:'20px'}}>
                  {item.img.map(v=>(
                    <img onClick={()=>this.toSee(v)} src={v} key={v} style={{width:'100px',height:'100px'}} alt=""/>
                  ))}
                </div>
              </div>
            </List.Item>
          )}
        />

        <PictureBrowing
          previewVisible={previewVisible}
          previewImage ={previewImage}
          previewonCancel={this.previewonCancel}
        />
      </div>
    )
  }
}
export default EvaluateDetails