import React from 'react'
import { Table, Button, Popconfirm, Icon, Switch, Form, Badge, Pagination } from 'antd'
import UserModal from  '../../component/modal/user_modal'
import './index.less'
import {connect} from 'react-redux'
import { dataAdress } from '../../redux/address_redux'


const orderStatus = ['','']
const invoiceStatus = ['','']
@connect(
  state=>state,
  {dataAdress}
)
class UserManage extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      selectedRowKeys: [], // Check here to configure the default column
      loading: false,
      addressVisible: false,
      editRow: {},
      modalType: "",
      footerNull:undefined,
      data:[
        {
          id:'1',
          key: '1',
          name:'',
          age: '23',
          gender: '',
          address_select: '',
          specific_address: ''
        },
      ]
    }
  }
  componentDidMount(){

  }
  //
  handleok = (val,value, title) => {
    this.setState({
      addressVisible: val,
      data: title === '' ? this.state.data.concat([value]) : this.state.data

    })
  }

  //
  modal = (type, record) => {
    console.log(type)
    if(type === '') {
      this.props.dataAdress(record)
      console.log(record)
      this.setState({
        modalType: type,
        currentData: record,
        record:record,
        footerNull:undefined
      }, () => {
             this.setState({
               addressVisible: true
             })
        // this.props.form.setFieldsValue({
        //   name: record.name,
        //   phone: record.phone,
        //   address_select: record.address_select,
        //   specific_address:record.specific_address,
        //   default:record.default
        // })
        this.setState({editRow: record})
        // console.log(this.state.editRow,record)
      })
    }
    if(type === ''){
      this.setState({
        record:[],
        modalType: type,
        currentData: record,
        footerNull:null
      }, () => {
                     this.setState({
                       addressVisible: true
                     })
      })
      // console.log(())
      // if (type === '') return;
    }
    // if(type === ''){
    //   this.setState({
    //     record:[],
    //     addressVisible: true,
    //     modalType: type,
    //   })
    //   if (type === '') return;
    // }
    else {
      this.setState({
        record:[],
        modalType: type,
        currentData: record,
        footerNull:undefined
      }, () => {
              this.setState({
                addressVisible: true
              })
      })
      // if (type === '') return;
    }
  }
  //close modal
  onCancel = () => {
    this.setState({
      addressVisible:false
    })
  }
  //
  onDelete = (record,index) => {
    console.log(index)
    const dataChange = this.state.data
    dataChange.splice(index, 1);
    this.setState({
      data:dataChange
    })
  }
  render() {
    const { addressVisible, modalType,footerNull}  = this.state
    const columns = [{
      title: '',
      dataIndex: 'name',
    },
      {
        title: '',
        dataIndex:'age',
      },
      {
        title: '',
        dataIndex: 'gender',
      },
      {
        title: '',
        dataIndex: 'address_select',
      },
      {
        title: '',
        dataIndex: 'specific_address',
      },
      {
        title: '',
        dataIndex: 'delete',
        render: (value, record, index) => {
          return (
            <div>
              <Button onClick={() => this.modal('', record, index)}></Button>
              <Button style={{marginLeft: '15px'}} onClick={() => this.modal('', record, index)}></Button>
              {/*<Popconfirm*/}
                {/*title=""*/}
                {/*onConfirm={() => this.onDelete(record, index)}*/}
              {/*>*/}
                {/*<Button style={{marginLeft: '15px'}}></Button>*/}
              {/*</Popconfirm>*/}
            </div>
          )
        }
      },
    ];
    return (
      <div className="container_manager_address container_width">
        <div className="shop_top container_top">
          {/*<h2>{menuName}</h2>*/}
          <h2></h2>
        </div>
        <div className="type_button">
          <Button
            type="primary"
            onClick={() => this.modal('')}
          >
            <Icon type='plus' />
            
          </Button>
        </div>
        <Table
          columns={columns}
          dataSource={this.state.data}
          onDelete={this.onDelete}
          rowKey={record => record.key}
          pagination={false}
        />
        <Pagination
          current={1} total={1} pageSize={1}
        />
        {
          addressVisible ? <UserModal
            data={this.state.currentData}
            addressVisible={addressVisible}
            title={modalType}
            onOk={this.handleok}
            onCancel={this.onCancel}
            footerNull={footerNull}
          />: null
        }
        
      </div>
    );
  }
}

export  default Form.create()(UserManage)