import React from 'react'
import { Table, Button, Popconfirm, Icon, Switch, Form, Badge, Pagination } from 'antd'
import ItemModal from  '../../component/modal/item_modal'
import './index.less'
import {connect} from 'react-redux'
import { dataAdress } from '../../redux/address_redux'


const orderStatus = ['','']
const invoiceStatus = ['','']
@connect(
  state=>state,
  {dataAdress}
)
class UserManage extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      selectedRowKeys: [], // Check here to configure the default column
      loading: false,
      itemVisible: false,
      editRow: {},
      modalType: "",
      footerNull:undefined,
      data:[
        {
          id:'1',
          key: '1',
          itemName:'1',
          itemPrice: 222.11,
          itemCount:20,
          itemDesc:'',
        },
      ]
    }
  }
  componentDidMount(){

  }
  //
  handleok = (visible,value, ) => {
    this.setState({
      itemVisible: visible,
      data: this.state.data.concat([value])
    })
  }

  //
  modal = (type, record) => {
    console.log(type)
    if(type === '') {
      this.props.dataAdress(record)
      console.log(record)
      this.setState({
        modalType: type,
        record: record,
        currentData: record,
        footerNull:undefined
      }, () => {
        this.setState({editRow: record})
      })
    }
    if(type === ''){
      this.setState({
        record:[],
        itemVisible: true,
        modalType: type,
        footerNull: null,
        currentData: record,
      })
    }
    else {
      this.setState({
        record:[],
        itemVisible: true,
        modalType: type,
        footerNull:undefined
      })
    }
  }
  //close modal
  onCancel = () => {
    this.setState({
      itemVisible:false
    })
  }
  //
  onDelete = (record,index) => {
    console.log(index)
    const dataChange = this.state.data
    dataChange.splice(index, 1);
    this.setState({
      data:dataChange
    })
  }
  render() {
    const { itemVisible, modalType,footerNull}  = this.state
    const columns = [{
      title: '',
      dataIndex: 'itemName',
    },
      {
        title: '',
        dataIndex:'itemPrice',
      },
      {
        title: '',
        dataIndex: 'itemCount',
      },
      {
        title: '',
        dataIndex: 'itemDesc',
      },
      {
        title: '',
        dataIndex: 'delete',
        render: (value, record, index) => {
          return (
            <div>
              {/* <Button onClick={() => this.modal('', record, index)}></Button> */}
              <Button style={{marginLeft: '15px'}} onClick={() => this.modal('', record, index)}></Button>
              <Popconfirm
                title=""
                onConfirm={() => this.onDelete(record, index)}
              >
                <Button style={{marginLeft: '15px'}}></Button>
              </Popconfirm>
            </div>
          )
        }
      },
    ];
    return (
      <div className="container_manager_address container_width">
        <div className="shop_top container_top">
          {/*<h2>{menuName}</h2>*/}
          <h2></h2>
        </div>
        <div className="type_button">
          <Button
            type="primary"
            onClick={() => this.modal('')}
          >
            <Icon type='plus' />
            
          </Button>
        </div>
        <Table
          columns={columns}
          dataSource={this.state.data}
          onDelete={this.onDelete}
          rowKey={record => record.key}
          pagination={false}
        />
        <Pagination
          current={1} total={1} pageSize={1}
        />
        <ItemModal
          data={this.state.currentData}
          itemVisible={itemVisible}
          title={modalType}
          onOk={this.handleok}
          onCancel={this.onCancel}
          footerNull={footerNull}
        />
      </div>
    );
  }
}

export  default Form.create()(UserManage)